<template>
  <form v-show="!isLoading" v-on:submit.prevent.stop="handleSubmit">
    <div class="form-group">
      <label for="name">Name</label>
      <input v-model="restaurant.name" id="name" type="text" class="form-control" name="name" placeholder="Enter name"
        required>
    </div>

    <div class="form-group mt-3">
      <label for="categoryId">Category</label>
      <select v-model="restaurant.categoryId" id="categoryId" class="form-control" name="categoryId" required>
        <option value="" selected disabled>
          --請選擇--
        </option>
        <option v-for="category in categories" :key="category.id" :value="category.id">
          {{ category.name }}
        </option>
      </select>
    </div>

    <div class="form-group mt-3">
      <label for="tel">Tel</label>
      <input v-model="restaurant.tel" id="tel" type="text" class="form-control" name="tel"
        placeholder="Enter telephone number">
    </div>

    <div class="form-group mt-3">
      <label for="address">Address</label>
      <input v-model="restaurant.address" id="address" type="text" class="form-control" placeholder="Enter address"
        name="address">
    </div>

    <div class="form-group mt-3">
      <label for="opening-hours">Opening Hours</label>
      <input v-model="restaurant.openingHours" id="opening-hours" type="time" class="form-control" name="opening_hours">
    </div>

    <div class="form-group mt-3">
      <label for="description">Description</label>
      <textarea v-model="restaurant.description" id="description" class="form-control" rows="3"
        name="description"></textarea>
    </div>

    <div class="form-group mt-3">
      <label for="image">Image</label>
      <img v-if="restaurant.image" :src="restaurant.image" class="d-block img-thumbnail mb-3" width="200" height="200">
      <input v-on:change="handleFileChange" id="image" type="file" name="image" accept="image/*"
        class="form-control-file">
    </div>

    <button :disabled="isProcessing" type="submit" class="btn btn-primary mt-4">
      {{ isProcessing ? '處理中' : '送出' }}
    </button>
  </form>
</template>

<script>
import adminAPI from '../apis/admin'
import { Toast } from '../utils/helpers'

const dummyData = {
  categories: [
    {
      id: 1,
      name: '中式料理',
      createdAt: '2019-06-22T09:00:43.000Z',
      updatedAt: '2019-06-22T09:00:43.000Z'
    },
    {
      id: 2,
      name: '日本料理',
      createdAt: '2019-06-22T09:00:43.000Z',
      updatedAt: '2019-06-22T09:00:43.000Z'
    },
    {
      id: 3,
      name: '義大利料理',
      createdAt: '2019-06-22T09:00:43.000Z',
      updatedAt: '2019-06-22T09:00:43.000Z'
    },
    {
      id: 4,
      name: '墨西哥料理',
      createdAt: '2019-06-22T09:00:43.000Z',
      updatedAt: '2019-06-22T09:00:43.000Z'
    }
  ]
}

export default {
  props: {
    initialRestaurant: {
      type: Object,
      default: () => {
        return {
          name: '',
          categoryId: '',
          tel: '',
          address: '',
          description: '',
          image: '',
          openingHours: ''
        }
      }
    },
    isProcessing: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      restaurant: {},
      categories: [],
      isLoading: true
    }
  },
  methods: {
    // fetchCategories() {
    //   this.categories = dummyData.categories
    // },
    async fetchCategories() {
      try {
        const { data } = await adminAPI.catagories.get()
        this.categories = data.categories
        this.isLoading = false
      } catch (error) {
        console.log('error', error)
        Toast.fire({
          icon: 'error',
          title: '無法取得餐廳類別，請稍後再試'
        })
      }
    },
    handleFileChange(e) {
      const { files } = e.target
      if (files.length === 0) {
        this.restaurant.image = ''
        return
      } else {
        const imageURL = window.URL.createObjectURL(files[0])
        this.restaurant.image = imageURL
      }
    },
    handleSubmit(e) {
      if (!this.restaurant.name) {
        Toast.fire({
          icon: 'warning',
          title: '請填寫餐廳名稱'
        })
        return
      } else if (!this.restaurant.categoryId) {
        Toast.fire({
          icon: 'warning',
          title: '請選擇餐廳類別'
        })
        return
      }

      const form = e.target
      const formData = new FormData(form)
      this.$emit('after-submit', formData)
    }
  },
  // 自動監控某些屬性，可以帶入兩個值 initialRestaurant(newValue, oldValue)
  // 如果屬性的內容有變更時，就可以去做一些事
  watch: {
    initialRestaurant(newValue) {
      this.restaurant = {
        ...this.restaurant,
        ...newValue
      }
    }
  },
  created() {
    this.fetchCategories()
    this.restaurant = {
      ...this.restaurant,
      ...this.initialRestaurant
    }
  }
}

</script>